<style lang="" scoped>
       * {
        padding: 0;
        margin: 0;
        list-style: none;
      }
  
      html,
      body {
        height: 100%;
        width: 100%;
      }
  
      body {
        overflow-x: hidden;
        background: rgba(240, 242, 245, 1);
      }
  
      .nav {
        width: 100%;
        height: 50px;
        background: #333333;
      }
  
      .nav .left {
        float: left;
        margin-left: 30px;
      }
  
      .nav .left span {
        color: #F4F4F4;
        line-height: 50px;
        font-size: 14px;
      }
  
      .nav .right {
        float: right;
        margin-right: 150px;
      }
  
      .nav .right li {
        height: 30px;
        margin-top: 10px;
        float: left;
        color: #F4F4F4;
        line-height: 30px;
        font-size: 14px;
        cursor: pointer;
        margin-left: 25px;
        text-align: center;
      }
  
      .nav .right li:hover {
        border-bottom: 3px solid #F4F4F4;
      }
  
      .nav .user {
        width: 150px;
        height: 40px;
        margin-top: 5px;
        float: right;
        line-height: 40px;
      }
  
      .nav .user .user_img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        overflow: hidden;
        float: left;
      }
  
      .nav .user .user_img img {
        width: 100%;
        height: 100%;
      }
  
      .nav .user .user_id {
        float: right;
        margin-right: 40px;
      }
  
      .nav .user .user_id span {
        color: #F4F4F4;
        font-size: 14px;
      }

      #breadApp .bread-header {
  width: 100%;
  height: 54px;
  background: rgba(255, 255, 255, 1);
  /* border: 1px solid #000; */
}

#breadApp .mainContent {
  /* height: 815px; */
  width: 97.5%;
  background: rgba(255, 255, 255, 1);
  /* border: 1px solid #000; */
  margin-top: 14px;
  margin-left: 1.25%;
  border-radius: 2px;
}

#breadApp .el-breadcrumb {
  margin-left: 32px;
}

#breadApp .el-breadcrumb span {
  line-height: 54px;
}

#breadApp .el-breadcrumb__item:last-child .el-breadcrumb__inner {
  font-weight: 400;
  color: rgba(51, 51, 51, 1);
}

#breadApp .el-breadcrumb__item:first-child {
  color: rgba(136, 136, 136, 1);
}

.baseMessage,
.firstMessage,
.secondMessage,
.proRun,
.ticktAndMoney {
  margin-left: 100px;
  padding-top: 20px;
}

.baseMessage_mainContent,
.firstMessage_mainContent,
.secondMessage_mainContent,
.proRun_mainContent,
.ticktAndMoney_mainContent {
  display: flex;
  flex-wrap: wrap;
  padding: 10px 0 10px 0;
}

.baseMessage span,
.firstMessage span,
.secondMessage span,
.proRun span,
.ticktAndMoney span {
  width: 300px;
  padding: 12px;
  margin-left: 20px;
  font-size: 14px;
  font-family:PingFangSC-Regular,PingFang SC;
  color: rgba(51, 51, 51, 1);
  font-weight: 400;
}
.mainContent .line {
  height: 1px;
  width: 98%;
  background-color:#E8E8E8;
  margin: auto;
}

#breadApp .el-row .el-button{
  height: 30px;
  width: 50px;
  font-size: 13px;
  line-height: 30px;
  float: left;
}

.bread-header {
  display: flex;
}

#breadApp .el-row {
  margin-top: 12px;
  margin-left: 80%;
}

</style>


